$default-font: "GothamPro", sans-serif;
$accent: #568F0A;
$text: #606060;
$wcolor: #fff;
$bcolor: #000;

/* ===========================main-style========================*/

.btn {
	position: relative;
	padding: 3px 20px;
	display: flex;
	align-items: center;
	border: 2px solid #ffffff;
	white-space: nowrap;
	overflow: hidden;
	transition: .5s;
	box-sizing: border-box;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: -1px;
		width: 100%;
		height: 0;
		background-color: #fff;
		transition: .8s;
		z-index: -1;
		border-radius: 0 0 50% 50%; }
	svg {
		margin-right: 15px; }
	&:hover {
		border: 2px solid darken($accent, 0);
		color: $accent;
		cursor: pointer;
		box-sizing: border-box;
		&::before {
			height: 190%; }
		svg {
			use {
				fill: $accent; } } } }
.btn-empty {
	position: relative;
	padding: 3px 20px;
	display: flex;
	align-items: center;
	white-space: nowrap;
	overflow: hidden;
	transition: .5s;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		bottom: -1px;
		width: 100%;
		height: 0;
		background-color: #fff;
		transition: .8s;
		z-index: -1;
		border-radius: 50% 50% 0 0; }
	&:hover {

		color: $accent;
		cursor: pointer;
		&::before {
			height: 190%; }
		svg {
			use {
				fill: $accent; } } } }
.btn-fill {
	position: relative;
	display: flex;
	align-items: center;
	white-space: nowrap;
	justify-content: center;
	background-color: $accent;
	color: #fff;
	overflow: hidden;
	transition: .5s;
	z-index: 1;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		bottom: -1px;
		width: 100%;
		height: 0;
		background-color: #fff;
		transition: .8s;
		border-radius: 50% 50% 0 0;
		box-sizing: border-box;
		z-index: -1; }
	&:hover {
		border: 1px solid $accent;
		box-sizing: border-box;
		color: $accent;
		cursor: pointer;
		&::before {
			box-sizing: border-box;
			height: 190%; }
		svg {
			use {
				fill: #fff; } } } }

.btn-green-border {
	position: relative;
	padding: 3px 20px;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 2px solid $accent;
	white-space: nowrap;
	overflow: hidden;
	font-size: 14px;
	font-weight: 500;
	letter-spacing: 0.7px;
	color: $accent;
	transition: .5s;
	box-sizing: border-box;
	background-color: transparent;
	z-index: 1;
	&::before {
		content: '';
		position: absolute;
		left: 0;
		top: -1px;
		width: 100%;
		height: 0;
		background-color: $accent;
		transition: .8s;
		z-index: -1;
		border-radius: 0 0 50% 50%; }
	svg {
		margin-right: 15px; }
	&:hover {
		border: 2px solid darken($accent, 0);
		color: #fff;
		cursor: pointer;
		box-sizing: border-box;
		&::before {
			height: 190%; }
		svg {
			use {
				fill: $accent; } } } }
/* ===========================Atom-class========================*/
.t25p {
	&::before {
		top: 25% !important; } }

.mt-40 {
	margin-top: 40px; }
.mt50 {
	margin-top: 50px; }
.mt60 {
	margin-top: 60px; }
.mt-90 {
	margin-top: 90px; }
.mt70 {
	margin-top: 70px; }
.mt90 {
	margin-top: 90px; }
.mb30 {
	margin-bottom: 30px; }
.pb50 {
	padding-bottom: 50px; }
.fw500 {
	font-weight: 500; }
.fz20 {
	font-size: 20px; }
.block {
	display: block; }
.color-accent {
	color: $accent; }
.color-accent-block {
	display: block;
	color: $accent; }
.ofh {
	overflow: hidden !important; }

.tac {
	display: block;
	text-align: center; }

/* ===========================Global_class========================*/
.leaves-green {
	padding-bottom: 45px;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	&::before {
		content: '';
		margin-right: 30px;
		width: 40px;
		height: 40px;
		background: url('../img/svg/leaves-w.svg') center center no-repeat; } }

.js-no-valid {
	border-bottom: 1px solid red !important; }



.menu-section.active-js {
	opacity: 1;
	visibility: visible; }

.menu-section {
	position: fixed;
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	background: rgba(153,204,51,0.9);
	z-index: 9;
	opacity: 0;
	visibility: hidden;
	transition: 0.5s;
	.menu-block-top {
		height: 50%;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		background: url('../img/bg-menu.jpg') center center no-repeat;
		background-size: cover;
		z-index: -2;
		.menu__word {
			padding-top: 80px;
			position: relative;
			font-size: 30px;
			font-weight: 300;
			line-height: 60px;
			text-transform: uppercase;
			letter-spacing: 2.1px;
			color: #fff;
			text-transform: uppercase;
			&::before {
				position: absolute;
				top: 78%;
				left: -55px;
				transform: translateY(-50%);
				content: '';
				display: inline-block;
				width: 40px;
				height: 34px;
				background: url('../img/svg/leaves-w-w.svg') center center no-repeat; } }
		&::before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background-color: rgba(0,0,0,0.6);
			z-index: -1; } }

	.menu-block-bottom {
		height: 50%;
		position: relative;
		background: url('../img/home/home-bg.jpg') bottom center no-repeat;
		background-size: cover;
		z-index: -2;
		&::before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			background-color: rgba(85,136,15,0.85);
			z-index: -1; }
		.menu-wrap {
			font-weight: 400;
			line-height: 42px;
			font-size: 20px;
			letter-spacing: 0.2px;
			a {
				color: #fff; }
			.header-menu {
				display: flex;
				justify-content: space-between;
				transform: translateY(-70px);
				z-index: 20;
				.has-children {
					a.menu__title {
						margin-bottom: 65px;
						display: block;
						font-size: 20px;
						text-transform: uppercase; }
					ul.sub-menu {
						list-style: square outside;
						li {
							position: relative;
							padding-left: 35px;
							&::before {
								content: '';
								width: 3px;
								height: 3px;
								position: absolute;
								top: 50%;
								left: 0;
								transform: translateY(-50%);
								background-color: #fff; }
							a {
								font-size: 16px;
								letter-spacing: 0.16px;
								font-weight: 300;
								position: relative;
								color: #fff;
								font-size: 16px;
								font-weight: 400;
								line-height: 30px;
								letter-spacing: 0.4px;
								&::after {
									position: absolute;
									bottom: -8px;
									left: 0;
									content: '';
									display: block;
									height: 1px;
									width: 100%;
									background-color: #fff;
									transform: scale(0); }
								&:hover {
									&::after {
										transition: 0.4s;
										transform: scale(1); } } } } } } } } } }

