/* ===========================main-style========================*/
@import url(../libs/slick-carousel/slick/slick.css);
@import url(../libs/animate.css/animate.min.css);
.btn {
  position: relative;
  padding: 3px 20px;
  display: flex;
  align-items: center;
  border: 2px solid #ffffff;
  white-space: nowrap;
  overflow: hidden;
  transition: .5s;
  box-sizing: border-box;
}

.btn::before {
  content: '';
  position: absolute;
  left: 0;
  top: -1px;
  width: 100%;
  height: 0;
  background-color: #fff;
  transition: .8s;
  z-index: -1;
  border-radius: 0 0 50% 50%;
}

.btn svg {
  margin-right: 15px;
}

.btn:hover {
  border: 2px solid #568f0a;
  color: #568F0A;
  cursor: pointer;
  box-sizing: border-box;
}

.btn:hover::before {
  height: 190%;
}

.btn:hover svg use {
  fill: #568F0A;
}

.btn-empty {
  position: relative;
  padding: 3px 20px;
  display: flex;
  align-items: center;
  white-space: nowrap;
  overflow: hidden;
  transition: .5s;
}

.btn-empty::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 0;
  background-color: #fff;
  transition: .8s;
  z-index: -1;
  border-radius: 50% 50% 0 0;
}

.btn-empty:hover {
  color: #568F0A;
  cursor: pointer;
}

.btn-empty:hover::before {
  height: 190%;
}

.btn-empty:hover svg use {
  fill: #568F0A;
}

.btn-fill {
  position: relative;
  display: flex;
  align-items: center;
  white-space: nowrap;
  justify-content: center;
  background-color: #568F0A;
  color: #fff;
  overflow: hidden;
  transition: .5s;
  z-index: 1;
}

.btn-fill::before {
  content: '';
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 0;
  background-color: #fff;
  transition: .8s;
  border-radius: 50% 50% 0 0;
  box-sizing: border-box;
  z-index: -1;
}

.btn-fill:hover {
  border: 1px solid #568F0A;
  box-sizing: border-box;
  color: #568F0A;
  cursor: pointer;
}

.btn-fill:hover::before {
  box-sizing: border-box;
  height: 190%;
}

.btn-fill:hover svg use {
  fill: #fff;
}

.btn-green-border {
  position: relative;
  padding: 3px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #568F0A;
  white-space: nowrap;
  overflow: hidden;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 0.7px;
  color: #568F0A;
  transition: .5s;
  box-sizing: border-box;
  background-color: transparent;
  z-index: 1;
}

.btn-green-border::before {
  content: '';
  position: absolute;
  left: 0;
  top: -1px;
  width: 100%;
  height: 0;
  background-color: #568F0A;
  transition: .8s;
  z-index: -1;
  border-radius: 0 0 50% 50%;
}

.btn-green-border svg {
  margin-right: 15px;
}

.btn-green-border:hover {
  border: 2px solid #568f0a;
  color: #fff;
  cursor: pointer;
  box-sizing: border-box;
}

.btn-green-border:hover::before {
  height: 190%;
}

.btn-green-border:hover svg use {
  fill: #568F0A;
}

