body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.65;
  font-family: "GothamPro", sans-serif;
  overflow-x: hidden;
  color: #606060;
  background-color: #fff;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

b, strong {
  font-weight: bold;
}

img {
  width: 100%;
}

img:focus {
  outline: none;
}

a {
  text-decoration: none;
}

a:hover {
  cursor: pointer;
}

button {
  border: none;
  text-decoration: none;
}

button:hover {
  cursor: pointer;
}

.button {
  margin: 0 auto;
  display: block;
  border: none;
  color: #fff;
  text-decoration: none;
  background-color: #568F0A;
  padding: 15px 45px;
  font-size: 13px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 3px;
  border-radius: 2px;
  text-align: center;
  position: relative;
  outline: none;
  transition: background-color .1s ease;
  cursor: pointer;
}

.button::after {
  transition: background-color .2s ease;
  position: absolute;
  content: '';
  height: 4px;
  bottom: 0;
  width: 100%;
  background-color: black;
  opacity: .18;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  left: 0;
}

.button:focus, .button:hover {
  text-decoration: none;
  color: #fff;
}

.button:hover {
  background-color: #64a70c;
}

.button:hover::after {
  opacity: .22;
}

.button:active {
  background-color: #487708;
}

.button:active::after {
  opacity: .32;
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.debug {
  z-index: 999999;
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (max-width: 780px) {
  .debug > div {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.debug > div > div > div {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(6.66667% - 20px);
  height: inherit;
  outline: 1px solid #ff0;
  background: rgba(0, 0, 0, 0.5);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

.productItems {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  justify-content: space-around;
}

.productItem {
  padding: 20px;
  margin: 20px 0;
  border-radius: 5px;
  background-color: #fff;
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
  width: calc(26.66667% - 20px);
}

@media screen and (max-width: 960px) {
  .productItem {
    width: calc(40% - 20px);
  }
}

@media screen and (max-width: 560px) {
  .productItem {
    width: calc(80% - 20px);
  }
}

.productItem__title {
  margin-top: 15px;
  color: #568F0A;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  font-weight: 500;
}

.productItem__descr {
  margin: 15px 0;
  font-size: 18px;
  font-weight: 400;
}

.productItem button {
  font-weight: 300;
}

