		border-radius: 50%;
		background-color: #689a23;
		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			display: block;
			content: '';
			transform: translate(-50%,0);
			border: 4px solid transparent;
			border-top: 4px solid #fff; }
		&:hover {
			cursor: pointer; } }
	.lang__item {
		a {
			position: relative;
			&::after {
				position: absolute;
				bottom: -2px;
				left: 0;
				content: '';
				display: block;
				height: 1px;
				width: 100%;
				background-color: #fff;
				transform: scale(0); }
			&:hover {
				&::after {
					transition: 0.4s;
					transform: scale(1); } } } }
	&__list {
		max-height: 20px;
		overflow: hidden;
