				background: #eaeaea url('../img/home/home-bg.jpg') center center no-repeat;
				background-size: cover; }
			&:nth-child(2) {
				height: 100vh;
				background: red url('../img/home/home__about.jpg') center center no-repeat;
				background-size: cover; } } }

	.slaierNav {
		padding: 55px 10px;
		padding-left: 180px;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: 585px;
		min-height: 312px;
		background-color: #ffffff;
		opacity: 0.9;
		box-sizing: border-box;
		&::after {
			content: "";
